import React, { Component } from 'react';
import '../styles/About.css';

// https://react.semantic-ui.com/


class About extends Component {

  render() {
    return (
      <div className="about" id="about">
        <h2>Quem sou eu:</h2>
        <p>
        Profissional do setor de tecnologia desde 2015 com projetos em empresas, como: DBServer, Yara Fertilizantes, Microsoft, Coob+ Viagens, Carrefour e Thoughtworks. Com expertise em desenvolvimento front-end, UX e UI para plataformas digitais, domínio do softwares de interface(Figma), Metodologias Ágeis, ChatGPT e desenvolvimento front-end utilizando: React.js, HTML5 e CSS3. 
            </p>
      </div>
    );
  }
}

export default About;