import React, { Component } from 'react';
import '../styles/Values.css';
import mouse from '../images/mouse.svg';
import share from '../images/share.svg';
import smartphone from '../images/smartphone.svg';
import hearth from '../images/hearth.svg';

// https://react.semantic-ui.com/


class Values extends Component {

  render() {
    return (
      <div className="diferenciais" id="diferenciais">
        <h2 id="title-page">Soft skills</h2>

        <div className="row">
          <div className="column-4" >
            <img src={mouse} className="service-icon" alt="Facilidade" />
            <h2>Pensamento crítico</h2>
            <p>Analisar e resolver problemas de forma inovadora.</p>
          </div>
          <div className="column-4" >
            <img src={share} className="service-icon image-border-radius" alt="Maior visibilidade" />
            <h2>Colaboração</h2>
            <p>Trabalhar bem com equipes diversas.</p>
          </div>
          <div className="column-4" >
            <img src={smartphone} className="service-icon image-border-radius" alt="Responsividade" />
            <h2>Comunicação</h2>
            <p>Explicar ideias de forma clara e objetiva.</p>
          </div>
          <div className="column-4" >
            <img src={hearth} className="service-icon " alt="Dedicação" />
            <h2>Empatia</h2>
            <p>Entender as necessidades e emoções dos usuários.</p>
          </div>
        </div>
      </div>
    );
  }
}

export default Values;