import React, { useState } from 'react';
import '../styles/Form.css';
import '../styles/Inputs.css';
import axios from 'axios';
import arquivo from '../arquivos/curriculo.pdf';


//////////
function Form() {
  const [campos, setCampos] = useState({
    name: '',
    email: '',
    phone: '',
    mensagem: '',
  });

  function handleInputChange(event) {

    campos[event.target.name] = event.target.value;
    setCampos(campos);
  }

  function send() {
    const formData = new FormData();
    Object.keys(campos).forEach(key => formData.append(key, campos[key]));
    axios.post('https://bott-digital-api.herokuapp.com/send',
      formData,
      {
        headers: {
          "Content-Type": `multipart/form-data; boundary=${formData._boundary}`,
          "Access-Control-Allow-Origin": "*"
        }
      })
      .then(response => { console.log("Tudo certo ", response.data); })
  }

  function handleFormSubmit(event) {
    event.preventDefault();
    document.getElementById("loading").style.visibility = 'visible';
    document.getElementById("sendMessageButton").disabled = true;
    send(campos);
    setTimeout(() => {

      document.getElementById("loading").style.visibility = 'hidden';
      document.getElementById("sendMessageButton").disabled = false;
      alert("Mensagem enviada");
    }, 3000);


  }
  return (

    <section class="contact-form" id="contact">
        <div class="container text-center">
          <h2 id="contact-title" class="mx-auto mb-5" >Currículo Completo</h2>
          <h4 id="contact-description" class="mx-auto mb-5" >Clique no botão abaixo para fazer o download do meu currículo completo e conhecer mais sobre minhas experiências e habilidades.</h4>
          <br></br>
          <a id="contact-button" class="btn btn-primary" href={arquivo}
             download >Download do curriculo</a>
        </div>
    </section>
  )
}

export default Form;